.slider {
	position: relative;
	margin-top: 40px;
	width: 100%;
}

.slider__title {
	@include setFont(18px, 22px, bold, $textColor-2);
	width: 150px;
}

.slider__slides {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	width: 100%;
}

.slider__slide {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.slider__content {
	margin-top: 60px;
	min-height: 300px;
	width: 100%;
}

.slider__controls {
	display: flex;
	justify-content: space-between;
	width: 90%;
	position: absolute;
	top: -10px;
	left: 50%;
	transform: translateX(-50%);
	z-index: 2;
}

.slider__button {
	position: relative;
	background-color: transparent;
	border: none;
	outline: none;
	width: 30px;
	height: 30px;
	cursor: pointer;

	&::after {
		position: absolute;
		content: "";
		top: 0;
		border: 25px solid transparent;
		border-right: 25px solid $bgColor;
		opacity: 0.6;
		transition: 0.3s ease-in;
	}

	&:hover::after {
		opacity: 1;
		transition: 0.3s ease-in;
	}
	
}

.slider__prev {
	&::after {
		left: -25px;
	}
}

.slider__next {
	&::after {
		transform: rotate(180deg);
		right: -25px;
	}
}