.about {
	padding-top: 40px;
	padding-bottom: 40px;
}

.about__image img {
	width: 100%;
}

.about__title {
	margin-top: 20px;
	padding: 10px;
	background-color: $bgColor;
	@include setFont(18px, 24px, bold, #fff);
	border-radius: 10px;
}

.about__description {
	margin-top: 20px;
	@include setFont(16px, 22px, normal, $textColor);
}

.about__item {
	position: relative;
	padding-left: 40px;
	margin-bottom: 10px;

	&::before {
		content: '';
		position: absolute;
		top: 3px;
		left: 0;

		width: 15px;
		height: 15px;
		background-color: $bgColor;
		border-radius: 5px;
	}
}