.services {
	padding-top: 40px;
	padding-bottom: 40px;
}

.services__content {
	margin-top: 20px;
}

.services__content-item {
	margin-bottom: 60px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.services__item-title {
	@include setFont(20px, 24px, bold, $textColor-2);
}

.services__content p {
	margin-top: 20px;
	@include setFont(16px, 20px, normal, $textColor);
}

.services__more {
	margin-top: 20px;
	align-self: flex-end;
}