.features {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bgColor-3;
}

.features__list {
	margin-top: 40px;
}

.features__img {
	display: block;
	width: 60px;
	height: 60px;
	background-size: contain;
	background-repeat: no-repeat;
}

.features__item {
	display: flex;
	justify-content: flex-start;
	margin-bottom: 20px;
	padding-top: 10px;
	padding-bottom: 10px;

	& p {
		margin-left: 40px;
		width: 60%;
	}

	&:first-child .features__img {
		background-image: url('../img/exp-icon.png');
	}

	&:nth-child(2) .features__img {
		background-image: url('../img/learning-icon.png');
	}

	&:nth-child(3) .features__img {
		background-image: url('../img/heart-icon.png');
	}
}