.contacts {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bgColor-3;
	width: 100%;
}

.contacts__container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contacts__adress {
	margin-top: 40px;
	@include setFont(22px, 26px, normal, $textColor);
	text-align: center;
}

.contacts__phone {
	margin-top: 40px;
	text-align: center;
	display: block;
	@include setFont(24px, 28px, bold, $textColor);

	&:hover {
		text-decoration: underline;
		transition: 0.3s;
	}
}

.contacts__map {
	margin-top: 40px;
}

.contacts__map iframe {
	width: 100%;
	height: 350px;

	border: 2px solid $bgColor-4;
	border-radius: 10px;
}

.contacts__button {
	margin-top: 40px;
}